.date-input-with-dialog-main {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  min-height: 1.6em;
  position: relative;
}
.date-input-with-dialog-input {
  position: absolute;
  width: 100%;
  height: 100%;
  text-align: right;
}
.date-input-with-dialog-input-buttons {
  display: block;
  position: absolute;
  margin: 0;
  padding: 0.1em;
  width: 1.3em;
  max-height: 1.3em;
  text-align: center;
  -ms-flex-align: center;
      align-items: center;
  border-radius: 5px;
  border: thin solid black;
  cursor: pointer;
}
.date-input-with-dialog-calendar {
  top: 0.2em;
  left: 1.8em;
}
.date-input-with-dialog-empty {
  top: 0.2em;
  left: 0;
}
.date-input-with-dialog-input-buttons:hover {
  color: blue;
  border: thin solid blue;
}



